import Ajax from "../../common/utils/myRequest.js"
export default {
	data() {
		return {
			requestCont:null
		}
	},
	onLoad:function(options){
		uni.showToast({
		    title: options.test,
		    duration: 2000,
			icon:"none"
		});
	},
	methods: {
		startRequest(){
			this.requestCont = new Ajax({url:"http://www.luojiaxin.com/api/allArticle"});
			this.requestCont.get().then(res=>{
				console.log(res)
			})
		},
		stopRequest(){
			this.requestCont.stop()
		}
	}
}
